@mixin mobile {
  @media screen and (max-width:480px) {
    @content;
  }
}
@mixin tablet {
  @media screen and (max-width:820px) {
    @content;
  }
}
@mixin pc {
  @media screen and (min-width:1200px) {
    @content;
  }
}
body{
  margin: 0;
}
.appcontainer{
  height: 100vh;
  display: flex;
  flex-direction: column;
  .nav{  
display: grid;
grid-template-columns: repeat(3, 1fr);  
background-color: #4C3A51;
padding: 5px;
  h1{
    font-size: 25px;
    margin-left: 20px;
    color: white;
  }
   .searchbar{  
  align-self: center;
    input{  
      font-size: 16px;
      padding: 10px 200px 10px 10px;
      background-color: #eaeaeb;
      color: #6c6c6c;
      border-radius: 10px;
      border:none;
      transition: all .4s;
    }
    input:focus{
      border:none;
      outline:none;
      box-shadow: 0 1px 12px #b8c6db;
      -moz-box-shadow: 0 1px 12px #b8c6db;
      -webkit-box-shadow: 0 1px 12px 
    }
    img{
      cursor: pointer;
      width: 20px;
      height: 20px;
      margin-left: -40px;
     margin-bottom: -5px;
    }
   }
  }
  .movies{
    padding: 50px;
    background-color: #774360;
 display: grid;
 grid-template-columns: repeat(3, 1fr);
 gap: 50px; 
    .poster{
      display: flex;
      justify-content: center;
      align-items: center;
      img{
        width: 200px;
        height: 300px;
      }
    }
  }
}